<template>
  <div class="modal fade" id="exampleModal" tabindex="-1" aria-hidden="true">
    <div class="modal-dialog modal-xl">
      <div
        class="
          modal-content
          cCC-bg-modal
          CC-up-modal
          d-flex
          justify-content-center
          align-items-center
        "
      >
        <div class="w-100 text-end">
          <button
            type="button"
            class="btn-close position-absolute CC-head-close"
            data-bs-dismiss="modal"
            aria-label="Close"
            @click.prevent="onClose"
          ></button>
        </div>

        <div
          class="
            modal-body
            d-flex
            flex-column
            uCC-gap-16
            align-items-center
            w-100
            p-5
          "
        >
          <form action="" method="" class="w-100">
            <div class="w-100 mt-3" dir="rtl">
              <label for="" class="form-label fCC-Regular-sB d-flex uCC-gap-8"
                >اسم المتجر كامل
                <i class="iCC-star-toggle d-flex CC-star-toggle"></i>
              </label>
              <div class="w-100">
                <input type="text" class="form-control" v-model="companyName" />
                <span class="error-feedback" v-if="v$.companyName.$error">
                  {{ v$.companyName.$errors[0].$message }}
                </span>
              </div>
            </div>
            <div class="w-100 mt-3" dir="rtl">
              <label
                for="post"
                class="form-label fCC-Regular-sB d-flex uCC-gap-8"
              >
                مجال عمل المتجر
                <i class="iCC-star-toggle d-flex CC-star-toggle"></i
              ></label>
              <input
                type="text"
                class="form-control CC-input"
                v-model="companyType"
              />
              <span class="error-feedback" v-if="v$.companyType.$error">
                {{ v$.companyType.$errors[0].$message }}
              </span>
            </div>
            <div class="d-flex row mt-3" dir="rtl">
              <span class="w-50 col-lg-6 col" dir="rtl">
                <label
                  for=""
                  class="form-label fCC-Regular-sB d-flex uCC-gap-8"
                >
                  المدينة
                  <i class="iCC-star-toggle d-flex CC-star-toggle"></i
                ></label>
                <select
                  id="country"
                  name="country"
                  class="form-control imCC-Background-model justify-content-end"
                  v-model="selected"
                  :class="icon"
                >
                  <i class="dd d-flex"></i>
                  <option
                    v-for="country in countrys"
                    :key="country"
                    :value="country.value"
                  >
                    {{ country.text }}
                  </option>
                </select>
              </span>
              <span class="w-50 col-lg-6 col" dir="rtl">
                <label
                  for=""
                  class="form-label fCC-Regular-sB d-flex uCC-gap-8"
                >
                  عدد الفروع
                  <i class="iCC-star-toggle d-flex CC-star-toggle"></i
                ></label>
                <input
                  class="form-control CC-input"
                  v-model.number="branches"
                  type="number"
                  :min="1"
                  :max="99"
                />
                <span class="error-feedback" v-if="v$.branches.$error">
                  {{ v$.branches.$errors[0].$message }}
                </span>
              </span>
            </div>
            <div class="w-100 mt-3" dir="rtl">
              <label for="" class="form-label fCC-Regular-sB d-flex uCC-gap-8">
                اسم صاحب المتجر
                <i class="iCC-star-toggle d-flex CC-star-toggle"></i
              ></label>
              <input type="text" class="form-control CC-input" v-model="name" />
              <span class="error-feedback" v-if="v$.name.$error">
                {{ v$.name.$errors[0].$message }}
              </span>
            </div>
            <div class="w-100 mt-3" dir="rtl">
              <label for="" class="form-label fCC-Regular-sB d-flex uCC-gap-8">
                عنوان المتجر
                <i class="iCC-star-toggle d-flex CC-star-toggle"></i
              ></label>
              <input
                type="text"
                class="form-control CC-input"
                v-model="adress"
              />
              <span class="error-feedback" v-if="v$.adress.$error">
                {{ v$.adress.$errors[0].$message }}
              </span>
            </div>
            <div class="d-flex row mt-3 justify-content-center">
              <span class="col-lg-6 col" dir="rtl">
                <label
                  for=""
                  class="form-label fCC-Regular-sB d-flex uCC-gap-8"
                >
                  رقم التواصل
                  <i class="iCC-star-toggle d-flex CC-star-toggle"></i
                ></label>
                <input
                  type="text"
                  class="form-control CC-input"
                  @input="acceptNumber"
                  v-model="number"
                />
                <span class="error-feedback" v-if="v$.number.$error">
                  {{ v$.number.$errors[0].$message }}
                </span>
              </span>
              <span class="col-lg-6 col" dir="rtl">
                <label
                  for=""
                  class="form-label fCC-Regular-sB d-flex uCC-gap-8"
                >
                  البريد الالكترونى
                  <i class="iCC-star-toggle d-flex CC-star-toggle"></i
                ></label>
                <input
                  type="email"
                  class="form-control CC-input"
                  v-model="email"
                />
                <span class="error-feedback" v-if="v$.email.$error">
                  {{ v$.email.$errors[0].$message }}
                </span>
              </span>
            </div>
          </form>
          <button
            type="button"
            @click="sendData()"
            class="
              fCC-Bold-sA
              w-100
              cCC-bg-main
              CC-send-button
              uCC-border-t-10
              cCC-text-white
              mt-3
            "
          >
            ارسال البيانات
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import useValidate from "@vuelidate/core";
import axios from "axios";
import {
  required,
  helpers,
  email,
  minLength,
  maxLength,
} from "@vuelidate/validators";
export default {
  name: "ModalForm",
  data() {
    return {
      icon: "iCC-drop-down",
      v$: useValidate(),
      companyName: "",
      companyType: "",
      branches: "",
      name: "",
      adress: "",
      number: "",
      email: "",
      selected: "A",

      countrys: [
        { text: "اختار المدينة", value: "A" },
        { text: "المدينة المنورة", value: "" },
        { text: "مكة", value: "" },
        { text: "الرياض", value: "" },
        { text: "الدوادمي", value: "" },
        { text: "المجمعة", value: "" },
        { text: "القويعية", value: "" },
        { text: "الأفلاج", value: "" },
        { text: "الزلفي", value: "" },
        { text: "الرياض", value: "" },
        { text: "الدوادمي", value: "" },
        { text: "المجمعة", value: "" },
        { text: "القويعية", value: "" },
        { text: "الأفلاج", value: "" },
        { text: "الزلفي", value: "" },
        { text: "الرياض", value: "" },
        { text: "الدوادمي", value: "" },
        { text: "المجمعة", value: "" },
        { text: "القويعية", value: "" },
        { text: "الأفلاج", value: "" },
        { text: "الزلفي", value: "" },
      ],
    };
  },

  validations() {
    return {
      companyName: {
        required: helpers.withMessage("اسم المتجر مطلوب", required),
        minLength: helpers.withMessage("أقل عدد 3حروف", minLength(3)),
        maxLength: helpers.withMessage("أكثر عدد 50حروف", maxLength(50)),
      },
      companyType: {
        required: helpers.withMessage("نوع المتجر مطلوب", required),
        minLength: helpers.withMessage("أقل عدد 3حروف", minLength(3)),
        maxLength: helpers.withMessage("أكثر عدد 50حروف", maxLength(50)),
      },
      countrys: { required: helpers.withMessage("المدينة مطلوبة", required) },
      branches: {
        required: helpers.withMessage("عدد الفروع  مطلوب", required),
        maxLength: helpers.withMessage("عدد الفروع غير صحيح", maxLength(2)),
      },
      name: {
        required: helpers.withMessage("الأسم مطلوب", required),
        minLength: helpers.withMessage("أقل عدد 3حروف", minLength(3)),
        maxLength: helpers.withMessage("أكثر عدد 25حروف", maxLength(25)),
      },
      adress: {
        required: helpers.withMessage("العنوان مطلوب", required),
        minLength: helpers.withMessage("أقل عدد 3حروف", minLength(3)),
        maxLength: helpers.withMessage("أكثر عدد 150حروف", maxLength(150)),
      },
      number: {
        required: helpers.withMessage(" رقم الهاتف مطلوب", required),
      },
      email: {
        required: helpers.withMessage("البريد الألكتروني مطلوب ", required),
        minLength: helpers.withMessage("أقل عدد 3حروف", minLength(3)),
        maxLength: helpers.withMessage("أكثر عدد 50حروف", maxLength(50)),
        email: helpers.withMessage("البريد الألكتروني غير صحيح ", email),
      },
    };
  },
  methods: {
    async sendData() {
      // console.log("Sign up now ....");
      this.v$.$validate();
      if (!this.v$.$error) {
        console.log("done");
        let result = await axios.post("http://localhost:3000/users", {
          companyName: this.companyName,
          companyType: this.companyType,
          countrys: this.countrys,
          branches: this.branches,
          name: this.name,
          adress: this.adress,
          number: this.number,
          email: this.email,
        });
        if (result.status == 201) {
          console.log("done");
          localStorage.setItem("user-info", JSON.stringify(result.data));
          this.$router.go(this.$router.currentRoute);
        }
      }
    },
    acceptNumber() {
      var x = this.number
        .replace(/\D/g, "")
        .match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
      this.number = !x[2]
        ? x[1]
        : "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
    },
    onClose() {
      this.$router.go(this.$router.currentRoute);
    },
  },
};
</script>
<style>
/*close icon  */
.CC-head-close {
  top: 10px;
  right: 10px;
  z-index: 1;
}

.error-feedback,
.error {
  color: red !important;
  margin-top: 5px;
}
.CC-star-toggle {
  width: 7px !important;
  height: 30px !important;
}
.CC-send-button {
  width: 520px;
  height: 56px;
  border: 0;
}
</style>